.nav {
    z-index: 1;
    position: fixed;
    top: 50px;
    left: 10px;
    display: flex;
    flex-direction: column;
    background-color: #E2DEDB;
    padding: 30px 20px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 
    0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
}

.nav a {
    text-decoration: none;
    color: #202020;
    text-transform: uppercase;
    font-size: 1.1rem;
    text-align: center;
    font-weight: bold;
    display: block;
    width: 70px;
    margin: 0 auto;
}

.nav a::after {
    content: "";
    display: block;
    width: 0;
    height: 3px;
    background: #202020;
    transition: all 0.3s ease-in-out;
}

.nav a:hover::after {
    width: 70px;
}

.nav a:not(:last-child) {
    margin-bottom: 20px;
}

@media screen and (max-width: 1090px) {
    .nav {
        left: initial;
        top: initial;
        bottom: 0px;
        width: 100%;
    }
}